.pages {
  margin-right: 20px;
}

.pdf-viewer {
  width: 100%;
  height: 100%;
}

.document .header {
  position: fixed;
  z-index: 20;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #fff;
}

.document .header h4 {
  margin: 0;
}

.document .left-panel {
  position: fixed;
  left: 50px;
}

.document .content {
  margin-top: 150px;
  display: flex;
  justify-content: center;
}

.document .editor-container {
  position: fixed;
  right: 50px;
}

.document .content .main {
  padding: 24px;
  min-height: 90%;
  background: #fff;
}

.document .footer {
  text-align: center;
}
